<script>
import {changeViewTime, getdata} from "../../request/api";
import ESApi from "../../api/elastic search";

export default {
  name: "authorRelationship",
  data() {
    return {
      isEmpty:false,
      datas: [{
        name: "jitendra malik",
        draggable: true,
        category: 0,
        symbolSize: 50,
        label: {
          show: true
        },
        state: 0
      },
        {
          name: 'automatic symbolic traffic scene analysis using belief networks',
          category: 1,
          num: "5EF39403",
          symbolSize: 20,
          state: 1,
          itemStyle: {
            normal: {
              color: '#000000'
            }
          }
        },
        {
          name: '潘海霞',
          category: 1,

        },
        {
          name: '梁灏然',
          category: 1,

        },
        {
          name: '黄润希',
          category: 1,

        },
        {
          name: '另一篇论文',
          category: 2,
          symbolSize: 20

        },
        {
          name: '张洁',
          category: 2,

        },
        {
          name: '黄文浩',
          category: 2,

        },
        {
          name: '想名字好难',
          category: 2,

        },
        {
          name: 'AAAA',
          category: 2,

        }
      ],
      linkmes: [
        {
          source: "jitendra malik",
          target: "automatic symbolic traffic scene analysis using belief networks",
          state: 4

        },
        {
          source: '谭火彬',
          target: '另一篇论文',
          state: 4
        },
        {
          source: '一篇论文',
          target: '潘海霞',
          state: 4
        },
        {
          source: '一篇论文',
          target: '梁灏然',
          state: 4
        },
        {
          source: '一篇论文',
          target: '黄润希',
          state: 4
        },
        {
          source: '谭火彬',
          target: '另一篇论文',
          state: 4
        },
        {
          source: '另一篇论文',
          target: 'AAAA',
          state: 4
        },
        {
          source: '另一篇论文',
          target: '张洁',
          state: 4
        },
        {
          source: '另一篇论文',
          target: '黄文浩',
          value: 'DNA',
          state: 4
        }, {
          source: '另一篇论文',
          target: '想名字好难',
          state: 4

        }, {
          source: '另一篇论文',
          target: '黄润希',
          state: 4
        }
      ]
    }
  },
  methods: {
    getdataSource() {
      getdata({
        author_id: this.$route.query.id
      }).then(res => {
        console.log(res)
        this.datas = res.datas
        this.linkmes = res.linkmes
        if (this.datas.length>0){
          this.isEmpty = false
          this.initCharts();
        }else {
          this.isEmpty = true
        }

      })
    },
    initCharts() {
      // 基于准备好的dom，初始化echarts实例
      let relaChart = this.$echarts.init(this.$refs.chart);
      // console.log(this.$props.data)
      // console.log(this.$props.linkm)
      // 绘制图表
      relaChart.setOption({
        backgroundColor: 'transparent',
        tooltip: {},
        animationDurationUpdate: 15000,
        animationEasingUpdate: 'quinticInOut',
        series: [
          {
            symbolSize: 20,
            type: 'graph',
            layout: 'force',
            force: {
              // gravity: 0  //引力
              edgeLength: 100, //默认距离
              repulsion: 200 //斥力
            },
            focusNodeAdjacency: true,
            roam: true,
            label: {
              show: false
            },
            data: this.datas,
            links: this.linkmes,
            // data:this.$props.data,
            // links: this.$props.linkm,
            lineStyle: {
              color: "source",
              width: 1,
            },
            categories: [
              // {name: '0'},
              // {name: '1'},
              // {name: '2'}
            ],
            itemStyle: {
              borderColor: 'black',
              borderWidth: 1,
              shadowBlur: 10,
              shadowColor: 'rgba(1,1,1,0.5)',
              color: 'rgb(82,82,82)'
              //               borderColor: "#fff",
              // borderWidth: 1,
              // shadowBlur: 10,
              // shadowColor: "rgba(0, 0, 0, 0.3)",
            },
            emphasis: {
              lineStyle: {
                width: 10,
              }
            }
          }
        ]
      })

      relaChart.on('click', (param) => {
        console.log(param)
        let state = param.data.state
        let id = param.data.num
        if (state == 2) {
          ESApi.getAuthorInfo(id).then(response => {
            console.log(response);
            if (response.data.hits.hits.length > 0) {
                    changeViewTime({
        author_id: id
      }).then(res => {
        console.log(res)
      })
              this.$router.push({
                path: '/authorPage',
                query: {
                  id: id
                }
              })
              this.$router.go(0)
            }

          })

        } else if (state == 1) {
          this.getVisit(id)
          this.$router.push({
            path: '/article/' + id + '/overviews'
          })
        }
        // this.$router.push({
        //     path: '/wyhTest',
        //     query: {
        //       name: param.name
        //     }
        //   }
        // )
      })

    },
    getVisit(id) {
      console.log('获取')
      this.axios({
        method: "post",
        // url:"http://139.9.132.83:8000/user/IsFavoritePaper",
        url: "http://139.9.132.83:8000/search/visitpaper",
        data: {
          paper_id: id
        }
      })
        .then(response => {
          console.log(response.data)
        })
    },
  },
  //一加载页面就调用
  mounted() {
    this.getdataSource()

  }
}
</script>

<style scoped>
